<template>

<div class="page">
    <div class="hd">
        <h1 class="page_title">Dialog</h1>
    </div>
    <div class="bd spacing">
        <a href="javascript:;" class="btn btn_primary" v-on:click="showAlert=true;">Dialog Alert</a>
        <a href="javascript:;" class="btn btn_primary" v-on:click="showConfirm=true;">Dialog Confirm</a>
    </div>

    <Dialog :show.sync="showAlert"></Dialog>

    <Dialog :show.sync="showConfirm" type="confirm" title="">
        
        <br> ...
    </Dialog>

</div>

</template>

<script>

import Dialog from './../../src/Dialog.vue'

export default {
    name: 'DialogView',
    components: {
        Dialog
        // Button
    },
    data() {
        return {
            showAlert: false,
            showConfirm: false
        };
    },
    created() {
        this.$on('on-alert-confirm', function() {
            this.showAlert = false;
        });
        this.$on('on-confirm-cancel', function() {
            alert('');
            // this.showConfirm = false;
        });
        this.$on('on-confirm-confirm', function() {
            // this.showAlert = true;
            alert('');
            this.showConfirm = false;
        })
    }
}

</script>



